import React from 'react'
import { Route as PureRoute, Switch as PureSwitch, useHistory } from 'react-router-dom'
import Loading from '@/component/status/Loading'
import PageNotFound from '@/component/status/404'
import { pathJoin } from './url'

/**
 *  module  () => import(...)
 *  Route
 *    component permission  PureRoute
 *    module permission 
 *      permission  true  module
 *      permission    Redirect 
 */
export function AsyncRoute({ module, ...rest }) {
  return (
    <React.Suspense fallback={<Loading />}>
      <Route {...rest} component={React.lazy(module)} module={module} />
    </React.Suspense>
  )
}

export function Route({ permission, path, ...rest }) {
  const history = useHistory()

  if (typeof permission === 'function') {
    const result = permission({
      history,
      ...rest
    })
    if (result) {
      if (typeof result.then === 'function') {
        return (
          <PureRoute
            path={path}
            component={
              React.lazy(() => result.then((t) => {
                if (t === true) {
                  return rest.module()
                }
                // permission 
                return {
                  default: () => t
                }
              }))
            }
          />
        )
      }
      return <PureRoute path={path} component={() => result} />
    }
  }
  return (
    <PureRoute {...rest} />
  )
}

export function Switch({ prefix = '', children }) {
  return (
    <>
      <PureSwitch>
        {children}
        <PureRoute path={pathJoin(prefix, '*')}>
          <PageNotFound />
        </PureRoute>
      </PureSwitch>
    </>
  )
}
