import React from 'react'
import { Switch, AsyncRoute } from '@/util/route'
import Navigation from './Navigation'

export default function Main() {
  return (
    <>
      <Navigation />
      <Switch>
        <AsyncRoute path="/doc" module={() => import('@/page/doc')} />
        <AsyncRoute path="/about" module={() => import('@/page/example/about')} />
        <AsyncRoute
          path="/user"
          module={() => import(
            /* webpackPrefetch: true */
            '@/page/example/user'
          )}
        />
      </Switch>
    </>
  )
}
